.app {
  font-family: 'Arial', sans-serif;
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
}

.header {
  background: #2c3e50;
  color: white;
  padding: 1rem;
  border-radius: 8px;
}

.header h1 {
  margin: 0 0 1rem 0;
}

.header nav a {
  color: white;
  margin-right: 1rem;
  text-decoration: none;
}

.header nav a:hover {
  text-decoration: underline;
}

button {
  background: #3498db;
  color: white;
  border: none;
  padding: 10px 20px;
  border-radius: 4px;
  cursor: pointer;
  font-size: 16px;
}

button:hover {
  background: #2980b9;
}